import { Component, OnInit,Injector } from '@angular/core';
declare var $:any;

@Component({
  selector: 'print-code',
  templateUrl: './print-code.component.html',
  styleUrls: ['./print-code.component.css']
})
export class PrintCodeComponent implements OnInit {
  printCSS: string[];
  printStyle: string;

  constructor(injector: Injector) {
    // this.printCSS = ['http://cdn.bootcss.com/bootstrap/3.3.7/css/bootstrap.min.css'];
    this.printCSS =  ['./print-code.component.css'];
    this.printStyle = 
    `
      .print_content{
          width: 100%;
          display: flex;
          flex-direction: column;
          align-items: center;
          
          
      }
      .print_content .print_item{
          margin-top: 20px;
          margin-bottom: 20px;
          
      }
    `;
   }

   printBtnBoolean = true;
   printComplete() {
       this.printBtnBoolean = true;
   }
   beforePrint() {
       this.printBtnBoolean = false;
   }

  ngOnInit() {
    
    $("#print").barcode("359475767240818688", "code128",{
      output:'css',       //渲染方式 css/bmp/svg/canvas
      //bgColor: '#ff0000', //条码背景颜色
      color: '#000',   //条码颜色
      barWidth: 2,        //单条条码宽度
      barHeight: 50,     //单体条码高度
      // moduleSize: 5,   //条码大小
      // posX: 10,        //条码坐标X
      // posY: 5,         //条码坐标Y
      addQuietZone: false  //是否添加空白区（内边距）
    });

    $("#print2").barcode("359475767240818688", "code128",{
      output:'css',       //渲染方式 css/bmp/svg/canvas
      //bgColor: '#ff0000', //条码背景颜色
      color: '#000',   //条码颜色
      barWidth: 2,        //单条条码宽度
      barHeight: 50,     //单体条码高度
      // moduleSize: 5,   //条码大小
      // posX: 10,        //条码坐标X
      // posY: 5,         //条码坐标Y
      addQuietZone: false  //是否添加空白区（内边距）
    });
    $("#print3").barcode("359475767240818688", "code128",{
      output:'css',       //渲染方式 css/bmp/svg/canvas
      //bgColor: '#ff0000', //条码背景颜色
      color: '#000',   //条码颜色
      barWidth: 2,        //单条条码宽度
      barHeight: 50,     //单体条码高度
      // moduleSize: 5,   //条码大小
      // posX: 10,        //条码坐标X
      // posY: 5,         //条码坐标Y
      addQuietZone: false  //是否添加空白区（内边距）
    });
  }

}



