import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { RemoteURL } from './../conf/remote.url.conf';

@Injectable()
export class HttpLogin{
    private body = {
        username:'admin',
        password:'admin'
    };
    constructor(private http:HttpClient,private remoteUrl:RemoteURL){
    
    }
    HttpLogin(user:string , pass:string):boolean{
        this.body.username = user;
        this.body.password = pass;
        var result='erro';
        this.http.post(this.remoteUrl.url+'/dologin',this.body).subscribe(
            // Successful responses call the first callback.
            (data:response) => {
                if(data.result=='ok'){
                    result = 'ok';
                }else{
                    result = 'erro';
                }
                console.log(data);

            },
            // Errors will call this callback instead:
            (err: HttpErrorResponse) => {
                if (err.error instanceof Error) {
                    // A client-side or network error occurred. Handle it accordingly.
                    console.log('An error occurred:', err.error.message);
                } else {
                    // The backend returned an unsuccessful response code.
                    // The response body may contain clues as to what went wrong,
                    console.log(`Backend returned code ${err.status}, body was: ${err.error}`);
                }
            });
        if(result=='ok'){
            return true;
        }else{
            return false;
        }
        
    }
}
interface response{
    result:string;
    errMsg:string;
    parm:object;
}


